import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/api/controllers/User';
import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import * as _ from 'underscore';
import { FileDetailsService } from 'src/app/api/controllers/FileDetails';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-create-movein',
  templateUrl: './create-movein.component.html',
  styleUrls: ['./create-movein.component.scss']
})

export class CreateMoveinComponent implements OnInit {

  movein:any;

  statusTypeData:any;
  blocksData:any;
  unitData:any;
  raisedByData:any;
  userDetails: any;
  apartmentBlockUnitId:any;

  apartmentBlockNumber:string = "";
  apartmentBlockUnitNumber:string = "";

  isMoveinSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  isDataLoaded:boolean = false;

  userName:string = "";

  documentConfigId:string = "";
  documentDataList:any;

  documentComment:string = "";

  isFileAdded:boolean = false;
  selectedFile: File;
  fileList: File[] = [];
  listOfFiles: any[] = [];
  reqUserId: any;
  uploadedDocumentList:any;
  getUserRole: any;
  min = new Date();
  showDateError: boolean = false;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private moveInOutService:MoveInOutService,
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private fileDetailsService:FileDetailsService
  ) { }


  getBlock() {

    let params = {
      apartmentBlockUnitId: parseInt(this.apartmentBlockUnitId)
    }

    this.apartmentService.getApartmentBlockUnitById(params).subscribe((res:any) => {
        this.apartmentBlockNumber = res[0].apartmentBlockNumber
        this.apartmentBlockUnitNumber = res[0].apartmentBlockUnitNumber
    },
    error => {
      console.log(error);
    }
    );
  }

  uploadFile(event){
    this.isFileAdded = true;

    for (var i = 0; i <= event.target.files.length - 1; i++) {
      var selectedFile = event.target.files[i];
      this.fileList.push(selectedFile);
      this.listOfFiles.push({'name':selectedFile.name, 'size':selectedFile.size});

      console.log(this.fileList[0]);
    }

  }

  deleteFile(index){
    // Delete the item from fileNames list
    this.listOfFiles.splice(index, 1);
    // delete file from FileList
    this.fileList.splice(index, 1);

    this.selectedFile = null;

    if(this.fileList.length == 0 && this.listOfFiles.length == 0){
      this.isFileAdded = false;
    }

  }

  bytesToSize(bytes) {
   if(bytes < 1024) return bytes + " Bytes";
    else if(bytes < 1048576) return(bytes / 1024).toFixed(3) + " KB";
    else if(bytes < 1073741824) return(bytes / 1048576).toFixed(3) + " MB";
    else return(bytes / 1073741824).toFixed(3) + " GB";
  }

  isFileEmpty(){
    return this.fileList.length == 0
  }
  dateChange() {
    this.showDateError = false;
  }
  submitCreateMoveInForm(form) {
    let selectedDate = new Date(this.movein.inDate);
    let now = new Date();
    if(selectedDate < now){
            this.showDateError = true;
          }
    else{
      this.isMoveinSubmitted = false;

      let details = {

        "inDate": this.movein.inDate,
        "inTime": moment(this.movein.inTime).format('HH:mm:ss'),
        "familyCount": this.movein.familyCount,
        "comments": this.movein.comment,
        "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
        "reqUserId": this.movein.reqUserId,
        "statusId": 372, //default approve status
        "approvedBy": null,
        "approvedDate": null,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": null,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "userName": this.userName,
        "mobile": this.raisedByData[0].phoneNumber,
        "email": this.raisedByData[0].emailId,
        "block_Unit": this.apartmentBlockNumber + ' ' + this.apartmentBlockUnitNumber,
        "requestType": "MoveIn",
        "statusName": "Pending Approval",
      }

      let params = {
        moveIn: details
      }

      this.moveInOutService.addMoveIn(params).subscribe((res:any) => {

        if(res.message){
         
          let file_params = {
            'file':this.fileList[0],
            'Description':this.documentComment,
            'InsertedBy':parseInt(this.cookieService.get('userId')),
            'ApartmentID':parseInt(this.cookieService.get('apartmentId'))
          }
          this.fileDetailsService.addFileDetailsWithFileOption(file_params).subscribe((res:any) => {
            let fileDetails = {

              "moveInId": parseInt(res.message),
              "moveInOutConfigId": parseInt(this.documentConfigId),
              "fileDetailsId": 0,
              "comments": this.documentComment,
              "isActive": true,
              "insertedBy": parseInt(this.cookieService.get('userId')),
              "insertedOn": new Date().toISOString(),
              "updatedBy": null,
              "updatedOn": null
  
            }
  
            let fileParams = {
              moveIn : fileDetails,
              file: this.fileList[0],
              apartmentId: parseInt(this.cookieService.get('apartmentId'))
            }
  
            this.moveInOutService.addMoveInDetails(fileParams).subscribe((res:any) => {
  
              if(res.message){
                this.isMoveinSubmitted = true;
                this.sharedService.setAlertMessage("Movein added successfully");
                this.router.navigate(['ams/moveinout-tracker/movein']);
              }
              else {
                this.isMoveinSubmitted = true;
                this.isError = true;
                this.alertMessage = res.errorMessage;
              }
  
            },
            error => {
  
              this.isMoveinSubmitted = true;
  
            });
  
          },
          error => {

           

          });

          
          

       }
       else {

        this.isMoveinSubmitted = true;
        this.isError = true;
        this.alertMessage = res.errorMessage;
           
       }

      },
      error => {

        this.isMoveinSubmitted = true;

      });

   

  }
  }

  ngOnInit() {
    this.getUserRole = this.cookieService.get('userRole');
  	this.movein = {};
    
      let params = {
        userId: parseInt(this.cookieService.get('userId'))
      }

      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(params).subscribe((res:any) => {

        if(res == undefined || res.length == 0){
          this.apartmentBlockUnitId = ""
        }
        else {
          this.apartmentBlockUnitId = "" + res[0].apartmentBlockUnitId
          this.getBlock();
        }

      },
      error => {
        
      });
    
    let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	//get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res:any) => {
      this.blocksData = res;
    });

    let statusParams = {
      LookupTypeId: 73
    }

    //status 
    this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res:any) => {
          
      this.statusTypeData = res.filter( item => {
          return item.isActive;
      });

    },
    error => {
      
    });

    let docParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.moveInOutService.getMoveInOutConfigByApartmentId(docParams).subscribe((res:any) => {

      this.documentDataList = res.filter(item => {
        return item.isActive;
      });


    }, error => {
        console.log(error);
    });

    let userParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    //get all users
    this.userService.getAllUsersByApartmentId(userParams).subscribe((res:any) => {
      this.userDetails = res;
      this.raisedByData = res;
      this.raisedByData = res.filter(item => {
            return item.userId == parseInt(this.cookieService.get('userId'))
        });
        this.movein.reqUserId = this.raisedByData[0].userId;
        this.userName = this.raisedByData[0].firstName+' '+ this.raisedByData[0].lastName;
        
        this.isDataLoaded = true;
    },
    error => {
      
    }
    );


  }

}